import React, { useState, useEffect } from "react";
import "../styles/global_styles.css";
import Axios from "axios";


export default function Appointments(props) {
  const [userId, setUserId] = useState("");
  const [appointmentList, setappointmentList] = useState([]);

  const getUserId = () => {
    Axios.post("https://hms-423.herokuapp.com/api/appointments/getuser", {
      email: props.email,
      password: props.password,
    }).then((response) => {
      console.log(response.data[0].id);
      setUserId(response.data[0].id);
    });
  };
  const bookAppointment = (id) => {
    Axios.post("https://hms-423.herokuapp.com/api/appointments/book", {
      id: id,
      pat_id: userId,
    }).then((response) => {
      console.log(response);
      console.log(response.statusText);
      if (response.statusText === "OK") {
        if (response.data.message === "Appointment Booked!") {
          alert("Successfully Booked!");
          getAppointmentList();
        }
      } else {
        alert("Booking Failed!");
      }
    });
    // setUserList([...userList, { email: email, password: password }]);
  };

  const getAppointmentList = () => {
    Axios.get("https://hms-423.herokuapp.com/api/appointments/get").then((response) => {
      setappointmentList(response.data);
      console.log(response);
      console.log(response.data);
    });
  };

  useEffect(() => {
    getAppointmentList();
    getUserId();
    // eslint-disable-next-line
  }, []);


  return (
    <>
      <div className="body">
      </div>
      <div className="page-body">
        <h2 className="AB">Appointment Booking</h2>
        <br />
        <div className="tab-container">
          <table class="table">
            <thead>
              <tr>
                <th scope="col">No.</th>
                <th scope="col">Date</th>
                <th scope="col">Time</th>
                <th scope="col">Book</th>
              </tr>
            </thead>
            <tbody>
              {appointmentList.map((val) => {
                return (
                  <tr>
                    <th scope="row">{val.ID}</th>
                    <td>{val.Date}</td>
                    <td>{val.Time}</td>
                    <td>
                      {val.is_Available === "true" ? (
                        <button
                          onClick={() => {
                            if (
                              window.confirm(
                                "Book Appointment on: " +
                                  val.Date +
                                  " at: " +
                                  val.Time
                              )
                            ) {
                              bookAppointment(val.ID);
                            }
                          }}
                          className="booke btn btn-primary"
                        >
                          Available
                        </button>
                      ) : (
                        <button className="booke btn btn-success">Booked</button>
                      )}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}
