import React, { useState } from "react";
import Axios from "axios";
import "../styles/auth_styles.css";
import { useNavigate, Link } from "react-router-dom";

export default function Signup() {
  const [name, setName] = useState("");
  const [regNo, setRegNo] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [gender, setGender] = useState("male");
  const [phone, setPhone] = useState("");
  const navigate = useNavigate();

  // const [userList, setUserList] = useState([]);

  // useEffect(() => {
  //   Axios.get("https://hms-423.herokuapp.com/api/get").then((response) => {
  //     setUserList(response.data);
  //     setRetrievedEmail(response.data[0].email);
  //     setRetrievedPassword(response.data[0].password);
  //     console.log(response);
  //   });
  // }, [email, password]);

  const registerUser = (event) => {
    Axios.post("https://hms-423.herokuapp.com/api/auth/register", {
      name: name,
      email: email,
      reg_no: regNo,
      password: password,
      gender: gender,
      phone: phone,
    }).then((response) => {
      console.log(response.statusText);
      if (
        email === "" ||
        password === "" ||
        name === "" ||
        regNo === "" ||
        phone === ""
      ) {
        alert("Fill all Credentials!");
      } else if (response.data.message === "Not-Found") {
        alert("User Existed!");
      } else if (response.statusText === "OK") {
        alert("Successfully Registered!");
        navigate("/");
      }
    });
    event.preventDefault();
    // setUserList([...userList, { email: email, password: password }]);
  };

  return (
    <>
      <div id="main-body">
        <div id="form-body">
          <div id="vl"></div>
          <div>
            <h1>SIGN UP</h1>
            <br />
            <form onSubmit={registerUser}>
              <input
                type="text"
                placeholder="Full Name"
                name="name"
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              <br />
              <input
                type="text"
                placeholder="Reg no."
                name="reg_no"
                onChange={(e) => {
                  setRegNo(e.target.value);
                }}
              />
              <br />
              <input
                type="email"
                placeholder="Email"
                name="email"
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
              />
              <br />
              <input
                type="tel"
                placeholder="Phone no."
                name="phone"
                onChange={(e) => {
                  setPhone(e.target.value);
                }}
              />
              <br />
              <select
                onChange={(e) => {
                  setGender(e.target.value);
                  console.log(e.target.value);
                }}
              >
                <option value="male" selected>
                  Male
                </option>
                <option value="female">Female</option>
                <option value="not-set">Rather Not To Say</option>
              </select>

              <br />
              <input
                type="password"
                placeholder="Password"
                name="password"
                onChange={(e) => {
                  setPassword(e.target.value);
                }}
              />
              <br />
              <button type="submit" id="login-btn">
                Sign Up
              </button>
              <br />
              <div>
                {" "}
                <p>
                  Already have an Account{" "}
                  <Link id="signup-text" to="/">
                    <b>Login</b>
                  </Link>
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
