import React, { useState } from "react";
import Axios from "axios";
import "../styles/auth_styles.css";
import { useNavigate, Link } from "react-router-dom";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [role] = useState("Student");
  const navigate = useNavigate();

  const loginUser = (event) => {
    Axios.post("https://hms-423.herokuapp.com/api/auth/login", {
      email: email,
      password: password,
      role: role,
    }).then((response) => {
      console.log(response);
      if (email === "" || password === "") {
        alert("Enter your Email and Password!");
      } else if (response.data.message === "Not Found!") {
        alert("Invalid Credentials!");
      } else if (response.statusText === "OK") {
        alert("Successfully Login!");
        navigate("/home", { state: { email: email, password: password } });
      }
    });
    event.preventDefault();
  };

  return (
    <>
      <div id="main-body">
        <div id="form-body">
          <div id="vl"></div>
          <div>
            <h1>LOGIN</h1>
            <br />
            <form onSubmit={loginUser}>
              <input
                type="email"
                placeholder="Email"
                name="Email"
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
              />
              <br />
              <input
                type="password"
                placeholder="Password"
                name="password"
                onChange={(e) => {
                  setPassword(e.target.value);
                }}
              />

              <br />
              {/* <div style={{ width: "90%" }}>
                <a id="forgot-password" href="/">
                  Forgot Password?
                </a>
              </div> */}
              <button type="submit" id="login-btn">
                Login
              </button>
              <br />
              <div>
                {" "}
                <p>
                  Don't have an Account?{" "}
                  <Link id="signup-text" to="/signup">
                    <b>Register</b>
                  </Link>
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
